<script>
import axios from "axios";

export default {
  name: "OurSkillSection",
  data () {
    return {
      isActive : false,
      interval: {},
      value1: 0,
      value2: 0,
      value3: 0,
      value4: 0,
      v1 : 95,
      v2 : 50,
      v3 : 100,
      v4 : 75,
    }
  },
  beforeDestroy () {
    clearInterval(this.interval)
  },
  mounted () {
    axios.get(this.$store.state.host + '/website/api/v1/website/statistics').then(response=>{
      this.v1 = response.data.data.tender_percent;
      this.v2 = response.data.data.competition_percent;
      this.v3 = response.data.data.other_percent;
      this.v4 = response.data.data.all_products_percent; // no data
      this.interval = setInterval(() => {
        if(this.isActive===true) {
          if (this.value1 < this.v1) {
            this.value1 += 1
          }
          if (this.value2 < this.v2) {
            this.value2 += 1
          }
          if (this.value3 < this.v3) {
            this.value3 += 1
          }
          if (this.value4 < this.v4) {
            this.value4 += 1
          }

          if((this.value4 === this.v4) && (this.value3 === this.v3) && (this.value2 === this.v2) && (this.value1 === this.v1)){
            clearInterval(this.interval);
          }
        }
      }, 20)
    })
  },

}
</script>

